import "core-js/modules/es.array.push.js";
import myEventBus from '@/assets/js/bus';
import { reactive, toRefs, onMounted } from 'vue';
export default {
  name: 'contents',
  setup() {
    const emitter = myEventBus();
    const data = reactive({
      isactive: true,
      num: 0,
      checkeds: [],
      middles: [{
        id: 'C000',
        title: '桂林'
      }, {
        id: 'C001',
        title: '七星区'
      }, {
        id: 'C002',
        title: '秀峰区'
      }, {
        id: 'C003',
        title: '象山区'
      }, {
        id: 'C004',
        title: '叠彩区'
      }, {
        id: 'C005',
        title: '临桂区'
      }, {
        id: 'C006',
        title: '灵川县'
      }, {
        id: 'C007',
        title: '阳朔县'
      }, {
        id: 'C008',
        title: '荔浦市'
      }, {
        id: 'C009',
        title: '全州县'
      }, {
        id: 'C010',
        title: '平乐县'
      }, {
        id: 'C011',
        title: '恭城县'
      }, {
        id: 'C012',
        title: '灌阳县'
      }, {
        id: 'C013',
        title: '资源县'
      }],
      rights: [{
        id: 'C014',
        title: '桂林',
        checked: true
      }, {
        id: 'C015',
        title: '七星区',
        checked: false
      }, {
        id: 'C016',
        title: '秀峰区',
        checked: false
      }, {
        id: 'C017',
        title: '象山区',
        checked: false
      }, {
        id: 'C018',
        title: '叠彩区',
        checked: false
      }, {
        id: 'C019',
        title: '临桂区',
        checked: false
      }, {
        id: 'C020',
        title: '灵川县',
        checked: false
      }, {
        id: 'C021',
        title: '阳朔县',
        checked: false
      }, {
        id: 'C022',
        title: '荔浦市',
        checked: false
      }, {
        id: 'C023',
        title: '全州县',
        checked: false
      }, {
        id: 'C024',
        title: '平乐县',
        checked: false
      }, {
        id: 'C025',
        title: '恭城县',
        checked: false
      }, {
        id: 'C026',
        title: '灌阳县',
        checked: false
      }, {
        id: 'C027',
        title: '资源县',
        checked: false
      }]
    });
    const leftclick = () => {
      data.isactive = !data.isactive;
    };
    const middleliclick = index => {
      data.num = index;
      emitter.emit('orders', data.middles[index].title);
    };
    const rightliclick = i => {
      if (i !== 0) {
        data.rights[0].checked = false;
        data.rights[i].checked = !data.rights[i].checked;
        if (data.rights[i].checked === true) {
          data.checkeds.push(data.rights[i].id);
          data.checkeds = [...new Set(data.checkeds)];
        } else {
          for (let j = 0; j < data.checkeds.length; j++) {
            if (data.rights[i].id === data.checkeds[j]) {
              data.checkeds.splice(j, 1);
              data.checkeds = [...new Set(data.checkeds)];
            }
          }
        }
        data.checkeds.length === 0 ? data.rights[0].checked = true : data.rights[0].checked = false;
        emitter.emit('changenum', data.checkeds.length);
      } else {
        // Object.assign(this.$data, this.$options.data.call(this))
        data.checkeds = [];
        data.checkeds.length = 0;
        emitter.emit('changenum', data.checkeds.length);
      }
    };
    onMounted(() => {
      emitter.emit('change', data.middles[0].title);
      emitter.on('clear', () => {
        // Object.assign(this.$data, this.$options.data.call(this))
        emitter.emit('change', data.middles[0].title);
        emitter.emit('changenum', data.checkeds.length);
      });
    });
    return {
      ...toRefs(data),
      leftclick,
      middleliclick,
      rightliclick
    };
  }
};